<template>
  <Interface>

    <div>
      <!-- <div class="weather-section background">
        
      </div> -->
      <div class="note-section background">
        <div class=" pa-3 d-flex justify-center align-center">
          <h2 class="ma-0"><b>Notifications</b></h2>
          <v-spacer></v-spacer>
        </div>

        <v-dialog
            v-model="dialogShow"
            max-width="600">
            <template v-slot:activator="{ on, attrs }">
            
            </template>

            <v-card
              class="mx-auto"
              max-width="600px"
              outlined>
              
              <v-card-text>
                  
              </v-card-text>
              <v-card-title class="text-h6">
                <v-icon 
                  class="mr-4"
                  :color="showedItem.color">
                  {{showedItem.icon}}
                </v-icon>

                {{showedItem.title}}
              </v-card-title>
              <v-card-text>
                <div class="text-overline d-flex">
                    {{showedItem.date}}
                    <v-spacer></v-spacer>
                    <v-icon>mdi-map-marker</v-icon>
                    {{showedItem.block_id}} - {{showedItem.small_block_id}}
                  </div>
                {{showedItem.comment}}
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="blue darken-1"
                  text
                  :to="{
                    name: 'SP'
                  }">
                  查看農場
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="closeItem">
                  關閉
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        
        <div v-for="(item, index) in notes">
          <v-card
            flat
            class="mx-auto"
            max-width="600"
            @click= "showItem(item)">
            <v-divider
              v-if="index!=0"
              class="ma-0">
            </v-divider>
            <v-list-item three-line>
              <v-icon 
                class="mr-4"
                :color="item.color">
                {{item.icon}}
              </v-icon>
              <v-list-item-content>
                <div class="text-overline">
                  {{item.date}}
                </div>
                <v-list-item-title class="text-h6">
                  {{item.title}}
                </v-list-item-title>
                <v-list-item-subtitle>          
                  {{item.comment}}
                </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </v-card>
        </div>
        
      </div>
    </div>
  </Interface>
</template>

<script>
import Interface from './Interface.vue'

export default {
  name: 'HomeView',
  components: {
    Interface
  },
  data() {
    return {
      dialogAdd: false,
      dialogShow: false,
      editedIndex: -1,
      showedItem: {
        title: '',
        block_id: '',
        small_block_id: '',
        comment: '',
      },
      editedItem: {
        title: '',
        block_id: '',
        small_block_id: '',
        comment: '',
      },
      defaultItem: {
        title: '',
        block_id: '',
        small_block_id: '',
        comment: '',
      },
      notes:[
        {
          color:"yellow darken-2",
          icon:"mdi-alert-octagram",
          title:"Warming",
          date: "Oct 4 5:43am",
          block_id: "第一溫室",
          small_block_id: "第二台攝影機",
          comment:"你的農場現在出現嚴重枯黃問題，現在的健康度為60%，溫度為28°C，濕度80%RH",
        },
        {
          color:"",
          icon:"mdi-chat",
          title:"Welcome to Smart Farm",
          date: "Oct 1 5:43pm",
          block_id: "",
          small_block_id: "",
          comment:"Welcome to use this service! Smart Farm can record health of the crops help you manage your farm well. Wish you have a nice day",
        }
      ],
      blocks:[
        'first',
        'second',
        'third',
      ],
      small_blocks:[
        'west',
        'east',
        'south',
        'north',
      ],
      required: (value) => !!value || 'Required.',
      error:'',
    }
  },
  methods: {
    showItem(item){
      this.editedIndex = this.notes.indexOf(item)
      this.showedItem = Object.assign({}, item)
      this.dialogShow = true
    },
    closeItem(){
      this.dialogShow = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
      this.error=""
    }
  },
}
</script>

